//
import freshIcon from '@/assets/img/details/waterico.png';
import huinongIcon from '@/assets/img/details/agricultureico.png';
import freshImg from '@/assets/img/details/detailWater.png';
import nongyeImg from '@/assets/img/details/nongyeImg.png';
import axios from '@/axios.js';
import timeValue from '@/assets/js/time.js';
import qs from 'qs';
export default {
  data() {
    return {
      //,ehcart
      title: '',
      ecahrtState: false,
      //
      url: '',
      echartURL: '',
      /**/
      teletextORdata: true,
      detailsCon: true,
      /*icon*/
      cityName: '',
      onLineTime: '',
      collectionTime: '',
      lat: '12.34',
      lon: '34.56',
      icon: '',
      /**/
      weiduList: [],
      weiduListIndex: 1,
      weiduListCityName: '',
      /**/
      detailspage: '',
      /**/
      detailsImg: nongyeImg,
      /**/
      wdShowFlag: true,
      //JSON
      farming: [{
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        }
      ],
      //json
      farmingBase: [{
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        }
      ],
      //
      freshWater: [{
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        },
        {
          "title": "PH",
          "value": ""
        },
        {
          "title": "",
          "value": ""
        }
      ],
      //echart
      farmingOld: [],
      echartList: '',
    }
  },
  components: {
    /**/
    'v-scroll': resolve => require(['@/components/scroll/index'], resolve)
  },
  watch: {
    /*,*/
    weiduListIndex: function () {
      if (this.teletextORdata === false) {
        if (this.detailspage.type === '6') {
          this.drawFarming()
        } else {
          this.freshWaterLine();
        }
      }
    },
    echartList: function(){
      if (this.detailspage.type === '2') { //
        if (this.echartList !== '') {
          this.ecahrtState = false;
          this.freshWaterLine();
        } else {
          this.ecahrtState = true;
        }
      }
    }
  },
  mounted() {
    //,
    this.detailspage = this.$route.query;
    if (this.detailspage !== '') {
      //
      this.ajaxData();
      if (this.detailspage.type === '2') {
        this.ajaxEchartData();
        this.title = ''
      } else {
        this.title = ''
      }
    }
  },
  methods: {
    //
    routerback() {
      this.$router.back(-1)
    },
    /**/
    onRefresh(done) {
      this.weiduListIndex = '0';
      this.ajaxData();
      done(); // call done
    },
    /**/
    ajaxData() {
      if (this.detailspage.type === '6') {
        this.url = '/huiYun/getYunDetailList?yunDeatilId='
      } else {
        this.url = '/freshwater/getFreshwaterList?freshwaterId='
      }
      axios.get(this.url + this.detailspage.cityid)
        .then((res) => {
          //
          this.lat = this.toDecimal(res.data.data.lat);
          this.lon = this.toDecimal(res.data.data.lon);
          if (this.detailspage.type === '6') {
            /**/
            this.cityName = res.data.data.orgname;
            this.onLineTime = res.data.data.onlinetime;
            this.collectionTime = res.data.data.lastupdatevaluetime;
            this.detailsImg = nongyeImg;
            this.icon = huinongIcon;
            this.weiduList = this.farming;
            this.weiduList[0].value = this.toDecimal(res.data.data.airtemlastvalue) + ''; //
            this.weiduList[1].value = this.toDecimal(res.data.data.temlastvalue) + '%'; //
            this.weiduList[2].value = this.toDecimal(res.data.data.windlastvalue) + 'km/h'; //
            this.weiduList[3].value = this.toDecimal(res.data.data.lightlastvalue) + 'lux'; //
            this.weiduList[4].value = this.toDecimal(res.data.data.phlastvalue); //ph
            this.weiduList[5].value = this.toDecimal(res.data.data.soilsollastvalue) + 's/cm'; //
            this.weiduList[6].value = this.toDecimal(res.data.data.temlastvalue) + ''; //
            this.weiduList[7].value = this.toDecimal(res.data.data.rainfalllastvalue) + 'mm'; //
            this.weiduList[8].value = res.data.data.dirlastvalue; //
            this.farmingOld = this.weiduList;
          } else {
            /**/
            this.cityName = res.data.data.place;
            this.onLineTime = timeValue.hms(res.data.data.time, res.data.data.lastUpdateTime);
            this.collectionTime = timeValue.Format(res.data.data.createTime);
            this.detailsImg = freshImg;
            this.icon = freshIcon;
            this.weiduList = this.freshWater;
            this.weiduList[0].value = res.data.data.waterGradesDo; //
            this.weiduList[1].value = this.toDecimal(res.data.data.do1) + 'mg/L'; //
            this.weiduList[2].value = this.toDecimal(res.data.data.nh3N) + 'mg/L'; //
            this.weiduList[3].value = res.data.data.waterGradesNh3N; //
            this.weiduList[4].value = this.toDecimal(res.data.data.codmn); //
            this.weiduList[5].value = this.toDecimal(res.data.data.ph); //ph
            this.weiduList[6].value = this.toDecimal(res.data.data.toc); //
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //ecahrt
    ajaxEchartData() {
      //echart
      this.echartURL = '/freshwater/listDetailPc?place=';
      //
      axios.get(this.echartURL + this.detailspage.cityname)
        .then((res) => {
          if (res.status === 200) {
            this.echartList = res;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    /**/
    tuwen() {
      if (this.teletextORdata === false) {
        this.teletextORdata = true;
        this.timeORreload = true;
        this.detailsCon = true;
        if (this.detailspage.type === '6') {
          this.weiduList = this.farmingOld;
        }
      }
    },
    /**/
    dataBase() {
      if (this.teletextORdata === true) {
        this.teletextORdata = false;
        this.timeORreload = false;
        this.detailsCon = false;
        if (this.detailspage.type === '6') { //
          this.weiduList = this.farmingBase;
          //
          if (this.weiduListIndex >= this.weiduList.length) {
            this.weiduListIndex = '0';
          }
          //echart
          this.drawFarming()
        } else { //
          if (this.echartList !== '') {
            this.ecahrtState = false;
            this.freshWaterLine();
          } else {
            this.ecahrtState = true;
          }
        }
      }
    },
    /**/
    freshWaterLine() {
      this.weiduListCityName = this.weiduList[this.weiduListIndex].title;
      let xData = [];
      for (var i = 0; i < this.echartList.data.measuredTime.length; i++) {
        xData.push(timeValue.echartTime(this.echartList.data.measuredTime[i]));
      }
      let yData = '';
      let unit = '';
      //y
      if (this.weiduListIndex === 0) {
        yData = this.echartList.data.waterGradesDo;
      } else if (this.weiduListIndex === 1) {
        yData = this.echartList.data.do1;
        unit = 'mg/L';
      } else if (this.weiduListIndex === 2) {
        yData = this.echartList.data.nh3N;
        unit = 'mg/L';
      } else if (this.weiduListIndex === 3) {
        yData = this.echartList.data.waterGradesNh3N;
      } else if (this.weiduListIndex === 4) {
        yData = this.echartList.data.codmn;
      } else if (this.weiduListIndex === 5) {
        yData = this.echartList.data.ph;
      } else if (this.weiduListIndex === 6) {
        yData = this.echartList.data.toc;
      }
      this.drawLine(xData, yData, unit)
    },
    /**/
    drawFarming() {
      this.weiduListCityName = this.weiduList[this.weiduListIndex].title;
      let xData = [];
      let yData = [];
      var unit = '';
      let chooseName = '';
      //
      if (this.weiduListIndex === 0) {
        unit = '';
      } else if (this.weiduListIndex === 1) {
        unit = '%';
      } else if (this.weiduListIndex === 2) {
        unit = 'km/h';
      } else if (this.weiduListIndex === 3) {
        unit = 'lux';
      } else if (this.weiduListIndex === 4) {
        unit = 'PH';
      } else if (this.weiduListIndex === 5) {
        unit = 's/cm';
      } else if (this.weiduListIndex === 6) {
        unit = '';
      } else if (this.weiduListIndex === 7) {
        unit = 'mm';
      }
      //echart
      axios.get('/huiYun/listHuiYunByDimension?orgName=' + this.cityName + '&deviceName=' + this.weiduListCityName)
        .then((res) => {
          if (res.status === 200) {
            //x
            xData = res.data.data.updataTime;
            //y
            yData = res.data.data.deviceValue;
            //echart
            this.drawLine(xData, yData, unit);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    /*echart*/
    drawLine(xData, yData, unit) {
      let myChart = this.$echarts.init(document.getElementById('myChart'));
      // 
      var option = {
        //echart
        textStyle: {
          fontSize: 12,
          color: '#333' // 
        },
        //echart
        title: {
          text: this.cityName + this.weiduListCityName + '',
          subtext: '',
          x: 'center',
          top: 60,
          textStyle: {
            fontSize: 14,
            fontWeight: 'bolder',
            color: '#333' // 
          }
        },
        //echart
        tooltip: {
          trigger: 'axis',
          triggerOn: 'click',
          axisPointer: {
            animation: false
          },
          textStyle: {
            fontSize: 15,
            color: '#fff' // 
          },
          formatter: '{b}<br/>' + this.weiduListCityName + ': {c}' + unit
        },
        legend: {
          data: [this.weiduListCityName, ''],
          left: 20,
          top: 110,
        },
        axisPointer: {
          link: {
            xAxisIndex: 'all'
          }
        },
        dataZoom: [{
            show: true,
            realtime: true,
            start: 80,
            end: 100,
            xAxisIndex: [0, 1],
            top: 20,
            width: '90%',
            left: '5%',
            height: 30,
            filterMode: 'empty'
          },
          {
            type: 'inside',
            realtime: true,
            start: 30,
            end: 70,
            xAxisIndex: [0, 1],
            filterMode: 'empty'
          }
        ],
        //echart
        grid: [{
          left: 80,
          right: 60,
          height: '30%',
          top: 165,
        }, {
          left: 0,
          right: 0,
          top: '0%',
          height: '0',
          width: '0'
        }],
        xAxis: [{
            type: 'category',
            boundaryGap: false,
            axisLine: {
              onZero: true
            },
            data: xData,
            axisLabel: {
              rotate: 50,
              textStyle: {
                rotate: 60,
                interval: 0,
                fontSize: 12,
                color: '#666', //
              }
            },
          },
          {
            gridIndex: 1,
            type: 'category',
            boundaryGap: false,
            axisLine: {
              onZero: true
            },
            data: xData,
            position: 'top'
          }
        ],
        yAxis: [{
            name: this.weiduListCityName + ' ' + unit,
            type: 'value'
          },
          {
            gridIndex: 1,
            name: '',
            type: 'value',
            inverse: true
          }
        ],
        series: [{
            name: this.weiduListCityName,
            type: 'line',
            symbolSize: 8,
            hoverAnimation: false,
            data: yData
          },
          {
            name: '',
            type: 'line',
            xAxisIndex: 1,
            yAxisIndex: 1,
            symbolSize: 8,
            hoverAnimation: false,
            data: yData
          }
        ]
      };
      // 
      myChart.setOption(option);
    },
    /**/
    toDecimal(x) {
      var f = parseFloat(x);
      if (isNaN(f)) {
        return;
      }
      f = Math.round(x * 100) / 100;
      return f;
    },
  }
}



