import Vue from 'vue';
import Vuex from 'vuex';
import axios from '../axios.js';
import router from '../router'
import qs from 'qs';
import bus from '../bus.js'
Vue.use(Vuex);
//img
import Img1 from '@/assets/img/map/shuidi.png'; //
import Img5 from '@/assets/img/map/agriculture_mk.png'; //
import liked from '@/assets/img/like/liked.png'; //
import unliked from '@/assets/img/like/unliked.png'; //
import agriculture from '@/assets/img/like/agriculture.png'; //
//state   
const state = {
  info: [],
  selindex: '', //path    
  changeimg: Img5,
  slectlike: false,
  isliked: unliked,
  type: 'air',
  detailspage: {
    cityname: '',
    cityid: 0,
    type: '',
  }
}
var gmap;
var watermap;

function pd() { // state.selindex  img  2pd(i)
  switch (state.selindex) {
    case '/huiYun/listMap':
      state.changeimg = Img5;
      state.type = 'agriculture';
      break;
    case '/freshwater/listFreshwater':
      state.changeimg = Img1;
      state.type = 'water';
      break;
    default:
      alert('')
  }
}

//marker 
function getWater(map, item, marker) {
  AMap.event.addListener(marker, "click", function () {
    //
    axios
      .get('/freshwater/getFreshwaterList', {
        params: {
          freshwaterId: item.id
        }
      })
      .then(res => {
        if (res.data.status === 'SUCCESS') {
          state.info = res.data.data
          var html = '<div class="hq-card air_jump"><h3 class="title calm">' + state.info.place + '</h3>' + '<h4>' + state.info.date + '</h4>' +
            '<div class="air_con2 clearfix air_jump">' +
            '<ul>' +
            '<li><div class="left"><span>PH </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.ph || '--') + '</div></li>' +
            '<li><div class="left"><span></span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.do1 || '--') + 'mg/L</div></li>' +
            '<li><div class="left"><span></span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.nh3N || '--') + 'mg/L</div></li>' +
            '<li><div class="left"><span> </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.waterGradesNh3N || '--') + '</div></li>' +
            '</ul>' +
            '</div>' +
            '</div>'
          var infoWindow = new AMap.InfoWindow({
            content: html,
            autoMove: true,
            closeWhenClickMap: true,
            offset: {
              x: -0,
              y: -0
            }
          });
          var openInfoWindow = function (e) {
            infoWindow.open(map, marker.getPosition());
            setTimeout(function () {
              $('.air_jump').click(function () {
                state.detailspage.cityname = state.info.place;
                state.detailspage.cityid = state.info.id;
                state.detailspage.type = '2'; //typewatch								
                router.push({
                  path: '/details',
                  query: state.detailspage
                }); //	
              })
            }, 300)
          };
          openInfoWindow();
        }
      })
      .catch(err => {
        console.log(err);
      });
  });
};

//marker
function getAgriculture(map, item, marker) {
  AMap.event.addListener(marker, "click", function () {
    //
    axios
      .get('/huiYun/getYunDetailList', {
        params: {
          yunDeatilId: item.yunid
        }
      })
      .then(res => {
        if (res.data.status === 'SUCCESS') {
          state.info = res.data.data;
          collecta(state.info.collected)
          var html = '<div class="hq-card air_jump"><h3 class="title calm">' + state.info.orgname + '</h3>' + '<h4>' + state.info.lastupdatevaluetime + '</h4>' +
            '<div class="air_con1 clearfix air_jump">' +
            '<ul>' +
            '<li><div class="left"><span> </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.airtemlastvalue || '--') + '</div></li>' +
            '<li><div class="left"><span> </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.temlastvalue || '--') + '</div></li>' +
            '<li><div class="left"><span> </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.windlastvalue || '--') + 'km/h</div></li>' +
            '<li><div class="left"><span> </span></div>' + '<div class="center"><span></span></div>' + '<div class="right">' + (state.info.dirlastvalue || '--') + '</div></li>' +
            '</ul>' +
            '</div>' +
            '</div>'
          var infoWindow = new AMap.InfoWindow({
            content: html,
            autoMove: true,
            closeWhenClickMap: true,
            offset: {
              x: -0,
              y: -0
            }
          });
          var openInfoWindow = function (e) {
            infoWindow.open(map, marker.getPosition());
            setTimeout(function () {
              $('.air_jump').click(function () { //								
                state.detailspage.cityname = state.info.orgname;
                state.detailspage.cityid = state.info.id;
                state.detailspage.type = '6'; //typewatch
                router.push({
                  path: '/details',
                  query: state.detailspage
                }); //	
                console.log(state.detailspage)
              })
            }, 300)

          };
          openInfoWindow();
        }
      })
      .catch(err => {
        console.log(err);
      });
  });
};
//  operationmoduleType00dataType1,2,3.dataRingIdiddataRingNameaction10
function collectsel(operation, moduleType, dataType, dataRingId, dataRingName, action, successmsg, failmsg, url1, meta) {
  state.iscollection = false;
  var data1 = {
    operation: operation,
    moduleType: moduleType,
    dataType: dataType,
    dataRingId: dataRingId,
    dataRingName: dataRingName,
    action: action,
    url: url1,
    meta: meta
  }
  axios
    .post('/dataService/curd', data1)
    .then(res => {
      if (res.data.status == 'SUCCESS') {
        console.log(successmsg)
        bus.$emit('map')
      } else {
        console.log(failmsg)
      }
    })
    .catch(err => {
      console.log(err);
    });
}
//
function collecta(isCollected) {
  state.slectlike = isCollected;
  if (state.slectlike) { //
    state.isliked = liked;
  } else { //
    state.isliked = unliked;
  }
}
//mutations
const mutations = {
  renderMap(state) { //
    var that = this;
    gmap = new AMap.Map('container', {
      resizeEnable: true,
      dragEnable: true,
      touchZoom: true,
      doubleClickZoom: true,
      view: new AMap.View2D({ //
        center: new AMap.LngLat(120.1, 31.1),
        zoom: 8
      }),
      topWhenMouseOver: true
    });
    watermap = new AMap.Map('fresh-container', {
      resizeEnable: true,
      dragEnable: true,
      touchZoom: true,
      doubleClickZoom: true,
      view: new AMap.View2D({ //
        center: new AMap.LngLat(120.1, 31.1),
        zoom: 8
      }),
      topWhenMouseOver: true
    });
  },
  renderMapDatasel(state, data) { // 
    gmap.clearMap();
    pd(); //
    data.forEach(function (item, i) {
      if (item.lon && item.lat) {
        var markerPosition = null,
          imageUrl = '',
          curIcon = null;
        markerPosition = new AMap.LngLat(item.lon, item.lat);
        curIcon = new AMap.Icon({
          image: state.changeimg,
          size: new AMap.Size(22, 22)
        });
        var marker = new AMap.Marker({ //marker
          icon: curIcon,
          //
          position: markerPosition,
          //
          offset: new AMap.Pixel(-14, -20),
          map: gmap
        });
      } else {
        return false; //marker
      }
      state.type === 'agriculture' && getAgriculture(gmap, item, marker);
    })
    //      gmap.setFitView();
  },
  renderMapDatasel1(state, data) { // 
    watermap.clearMap();
    data.forEach(function (item, i) {
      if (item.lon && item.lat) {
        var markerPosition = null,
          imageUrl = '',
          curIcon = null;
        markerPosition = new AMap.LngLat(item.lon, item.lat);
        curIcon = new AMap.Icon({
          image: Img1,
          size: new AMap.Size(22, 22)
        });
        var marker = new AMap.Marker({ //marker
          icon: curIcon,
          //
          position: markerPosition,
          //
          offset: new AMap.Pixel(-14, -20),
          map: watermap
        });
      } else {
        return false; //marker
      }
      getWater(watermap, item, marker)
    })
  }
}
export default new Vuex.Store({
  state,
  mutations
})



